@import './Global.css';

.projects-container {
    margin-top: 15vh;
    margin-left: 5vw;
}

.project-card {
    display: flex;
    flex-direction: column;
    height: 60vh;
    background-color: var(--background-color-tertiary);
    width: 25vw;
    border-radius: 10px;
    margin-top: 5%;
}

.project-card:hover {
    transform: translateY(-2.5%);
    transition: all 0.3s ease-in-out;
}

.project-card:not(:hover) {
    transform: translateY(2.5%);
    transition: all 0.3s ease-in-out;
}

.project-card-links {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 5%;
    margin-top: auto;
    width: 90%;
    margin-left: 5%;
}

.project-card-links a {
    width: 8%;
    height: auto;
    display: flex;
    position: fixed;
    bottom: 10px;
    scroll-margin-inline-start: 10%;
}

.project-card-links a img {
    width: 100%;
    height: auto;
    object-fit: contain;
}

.project-card img {
    width: 90%;
    height: 50%;
    margin-inline: 5%;
    margin-top: 5%;
    object-fit: cover;
    border-radius: 10px;
}

.project-card h1 {
    font-size: 1.5rem;
    font-weight: 600;
    color: #fff;
    margin-inline: 5%;
}

.project-card p {
    font-size: 1rem;
    font-weight: 400;
    color: #fff;
    margin-inline: 5%;
}

.card-gallery {
    display: flex;
    width: 90vw;
    margin: 0 auto;
    flex-direction: row;
    gap: 5%;
    flex-wrap: wrap;
    align-items: center;
    justify-content: left;
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    transition: all ease-in-out 1s;
}

.modal-image {
    max-width: 90%;
    max-height: 90%;
    transition: all ease-in-out 1s;
}