@import '../styles/Global.css';

.contact-container {
    margin-left: 10vw;
    margin-top: 10vh;
}

.contact-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
    border-radius: 20px;
    background-color: var(--background-color-tertiary);
    width: 40vw;
    height: 70vh;
}

.contact-form input {
    color: #fff;
    background-color: transparent;
    border: none;
    border: 1px solid #fff;
    padding: 10px;
    border-radius: 5px;
    width: 90%;
    height: 2vh;
    margin: 2%;
}

.contact-form textarea {
    margin: 2%;
    color: #fff;
    background-color: transparent;
    border: none;
    border: 1px solid #fff;
    padding: 10px;
    border-radius: 5px;
    width: 90% !important;
    height: 50% !important;
    resize: none;
    font-family: var(--font-family);
}

.contact-form button {
    color: #fff;
    background-color: transparent;
    border: 1px solid #fff;
    border-radius: 10px;
    width: 94%;
    padding: 10px;
    margin: 2%;
    cursor: pointer;
}

.contact-form button:hover {
    border: 1px solid var(--accent-color);
    transition: 0.3s ease-in-out;
    color: var(--accent-color);
}